import React, {useState , useEffect} from 'react';
import Loading from './Loading';
import AllTours from './AllTours'
import './Tours.css';

const url = 'https://course-api.com/react-tours-project'
const ToursMain = ()=>{
 
 const [loading,setLoading] = useState(true)
 const [tours ,setTours] = useState([]);
 const RemoveTour=(id)=>{
  let newTours = tours.filter((tour)=> tour.id !== id)
  setTours(newTours)
 }
 const fetchTours = async()=>{
  setLoading(true)
  try {
   const response = await fetch(url);
   const tours = await response.json();
   setLoading(false);
   setTours(tours)
  } catch(error){
   setLoading(false)
   console.log(error)
  }
 };
 useEffect(()=>{
  fetchTours();
  document.title='Tours App';
 },[])
 if(loading){
  return<>
  <main>
   <Loading />
  </main>
 </>
 } 
 else if(tours.length ===0){
   return(
    <div className="container2">
     <h1  className='header loading'>No Tours Left</h1>
     <button className=' loading btn' onClick={()=> window.location.reload()}> Refresh</button>
    </div>
   )
 }
 else{
   return<>
   <main>
    <AllTours tours = {tours} RemoveTour={RemoveTour}/>
   </main>
 </>
 }
}

export default ToursMain;